@import url('https://fonts.googleapis.com/css2?family=Baloo+Tammudu+2&display=swap');
*
{
    margin: 0 ;
    padding : 0 ;
    box-sizing: border-box ;
    font-family: 'Baloo Tammudu 2', cursive;
    color : white ;
}
body
{
    text-align: center;
    align-items: center;
    background-color: black;
    height : 100vh ;
    width : 100% ;
}
.dot
{
    font-weight: bold;
    animation: dot-anime 1s infinite alternate-reverse;
}
@keyframes dot-anime {
    from{
        color : blue ;
        font-size: 25px ;
    }
    to{
        color : red ;
        
        
    }
}
.full-page
{
    /* margin-top: 120px ; */
    box-shadow: 0px 0px 24px 2px #d5d5d5;
    height : inherit ;
    width : 80% ;
    text-align: center;
    align-items: center;
    margin: 20px auto ;
    align-items: center;
}
.cards
{
    /* background-color: brown ; */
    display: grid;
    grid-template-columns: 1fr;
    height : inherit ;
    justify-content: center;
    align-items: center;
}
.card
{
    /* background-color: blue ; */
    /* box-shadow: 0px 0px 7px 2px black; */
    border-bottom: 2px solid #d5d5d5;
    height : 150px ;
    width : 260px ;
    margin : 10px auto ;
    display: flex;
    flex-direction: column;
    text-align: center;
    align-items: center;
    justify-content: center;
}
.tags
{
    font-weight: bold ;
    color : darkgrey ;
    animation: dot-anime2 1s infinite alternate-reverse;   
}
@keyframes dot-anime2 {
    from{
        color : white;
        /* font-size: 3px ; */
    }
    to{
        color : darkorange;        
        /* font-size: 6px; */
    }
}
@media only screen and (min-width: 1200px) {
    body
    {
        text-align: center;
        align-items: center;
        background-color: black;
        height : 100vh ;
        width : 100% ;
    }
    .dot
    {
        font-weight: bold;
        animation: dot-anime 1s infinite alternate-reverse;
    }
    @keyframes dot-anime {
        from{
            color : blue ;
            font-size: 10px ;
        }
        to{
            color : red ;
            
            
        }
    }
    .full-page
    {
        /* margin-top: 120px ; */
        box-shadow: 0px 0px 24px 2px #d5d5d5;
        height : inherit ;
        width : 80% ;
        text-align: center;
        align-items: center;
        margin: 20px auto ;
        align-items: center;
    }
    .cards
    {
        /* background-color: brown ; */
        display: grid;
        grid-template-columns: 1fr 1fr  ;
        height : inherit ;
        justify-content: center;
        align-items: center;
    }
    .card
    {
        /* background-color: blue ; */
        /* box-shadow: 0px 0px 7px 2px black; */
        border-bottom: 2px solid #d5d5d5;
        height : 130px ;
        width : 350px ;
        margin : 10px auto ;
        display: flex;
        flex-direction: column;
        text-align: center;
        align-items: center;
        justify-content: center;
    }
    .tags
    {
        font-weight: bold ;
        color : darkgrey ;
        animation: dot-anime2 1s infinite alternate-reverse;   
    }
    @keyframes dot-anime2 {
        from{
            color : white;
            /* font-size: 2px ; */
        }
        to{
            color : darkorange;
            
            
        }
    }  
}

















